.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
}

.modal-content {
    position: relative;
    width: 916px;
    height: 700px;
}

.modal-description {
    width: 916px;
    height: 114px;
    background: #1f1b16;

    display: flex;
    flex-direction: column;
    padding: 0px 18px 8px;
    font-family: "Zen Kurenaido", sans-serif;
    font-weight: 400;
    font-size: 20px;
}

.modal-content img {
    width: 916px;
    height: 586px;
    object-fit: cover;
}

.modal-description h3 {
    margin-top: 6px;
    border-left: 2px solid #1f1b16;
    padding-left: 4px;
    margin-left: -6px;
    text-transform: uppercase;
    color: #e4b474;
}

.modal-description span {
    color: #e2d4c4;
    font-size: 15px;
    margin-top: -14px;
}

.result-container-archive-description {
    height: 45px;
    display: flex;
    flex-direction: column;
    padding: 0px 18px 8px;
    font-family: "Zen Kurenaido", sans-serif;
    font-weight: 400;
    font-size: 20px;
}

.result-container-archive-description h3 {
    margin-top: 0%;
    text-transform: uppercase;
}

.result-container-archive-description span {
    font-size: 15px;
    margin-top: -14px;
}

.result-container-archive {
    width: 393px;
    height: 345px;
    display: flex;
    flex-direction: column;
    transition: background-color 0.3s ease;
}

.result-container-archive img {
    height: 299px;
    width: 100%;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
    object-fit: contain;
    background: white;
}

.result-container-archive:hover {
    transition: background-color 0.3s ease;
    background: #1f1b16;
    color: #e2d4c4;
    transform: scale(1.05);
}

.result-container-archive:hover h3 {
    transition: background-color 0.3s ease;
    color: #e4b474;
    text-decoration: underline;
    /* border-left: 2px solid #E4B474; */
    text-underline-offset: 5px;
}

.result-container-archive-description {
    height: 46px;
    display: flex;
    flex-direction: column;
    padding: 0px 18px 8px;
    font-family: "Zen Kurenaido", sans-serif;
    font-weight: 400;
    font-size: 20px;
    text-align: center;
}
