.header-layout {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100px;
    background: #1f1b16;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 34px 130px;
    box-sizing: border-box;
    z-index: 10;
}

.header-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.site-main-header-header-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.links {
    display: flex;
    gap: 53px;
    margin-bottom: 10px;
}

.link {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
    font-family: "Zen Kurenaido";
    font-weight: 400;
    font-size: 26px;
}

.link:hover {
    color: #e4b474;
}

.globus-button {
    display: flex;
    align-items: center;
    outline: none;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
}

.globus-button svg {
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: filter 0.3s ease;
    color: #e4b474;
}

.language-menu {
    width: 140px;
    height: 120px;
    background-color: #453f38;
    border-radius: 10px;
    padding: 8px;
    padding-top: 10px;
    padding-left: 12px;
    box-sizing: border-box;
    position: absolute;
    top: 110px;
    left: 93.5%;
    transform: translateX(-50%);
    z-index: 1000;
}

.language-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    color: #efe3d4;
}

.language-menu li {
    font-family: "Zen Kurenaido", sans-serif;
    font-size: 18px;
    margin-bottom: 10px;
    transition: color 0.3s ease;
    position: relative;
    padding-left: 15px;
    cursor: pointer;
}

.language-menu li.active {
    color: #e4b474;
}

.language-menu li.active::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 5px;
    height: 5px;
    background-color: #e4b474;
    border-radius: 50%;
}

.language-menu li:hover {
    color: #e4b474;
}
