.section-prisoner {
    display: flex;
    flex: 3;
    flex-direction: column;
    color: #1f1b16;
    width: 100%;
    box-sizing: border-box;
    padding: 107px 210px 131px 224px;
    background: url("../../images/DefaultBG.jpg") no-repeat center center;
    background-size: cover;
}

.container-carousel,
.materials-carousel,
.materials-carousel h4,
.elements-carousel,
.element-carousel {
    padding: 0;
    margin: 0;
    border: 0;
}

.container-header-prisoner {
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.container-header-prisoner-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
}

.header-of-container-prisoner {
    color: #1f1b16;
    font-family: "Oswald", sans-serif;
    font-size: 50px;
    font-weight: 400;
    text-align: center;
}

.container-header-prisoner h1 {
    width: -webkit-fill-available;
    margin: 0;
    /* margin-left: -96px;

     margin-right: -58px; 
    margin-right: -158px; */
    margin-block-start: 0em;
    margin-block-end: 0em;
    text-transform: uppercase;
}

.container-header-prisoner ul {
    list-style: none;
    padding: 0;
    margin-bottom: 46px;
    padding-bottom: 36px;
    font-size: 20px;
    font-family: "Inter", sans-serif;
    border-bottom: 2px solid #1f1b16;
}

.container-header-prisoner ul li {
    margin-bottom: 10px;
}

.container-header-prisoner ul li h2 {
    display: inline;
    font-size: 18px;
    font-weight: 700;
}

.container-header-prisoner ul li strong {
    margin-left: 0%;
    margin-top: 10px;
}

.container-story-prisoner {
    /* margin-top: 20px; */
    font-size: 16px;
    font-family: Inter;
    clear: both;
    width: 100%;
    font-size: 20px;
}

.container-story-prisoner p {
    margin-block-start: 0em;
    margin-block-end: 0em;
}

.container-prisoner-portrait {
    position: relative;
    float: left;
    margin-right: 70px;
    margin-left: 20px;
}

.container-prisoner-portrait::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 4px solid #000;
    z-index: 1;
}

.img-prisoner-portrait {
    width: 550px;
    height: 400px;
    object-fit: cover;
    position: relative;
    top: -20px;
    left: 20px;
    z-index: 2;
}

.container-prisoner-button {
    display: flex;
    flex-direction: column;
    gap: 23px;
    align-self: center;
    margin-top: 56px;
}

.container-prisoner-button-map {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 25px;
}

.container-carousel {
    width: 80%;
    margin: 50px auto;
}

.materials-carousel {
    width: 100%;
    padding: 15px 0 20px 25px;
    background: #1f1b16;
}

.materials-carousel h4 {
    font:
        normal 500 25px "Inter",
        sans-serif;
    color: #e9c79a;
}

.elements-carousel {
    margin-top: 10px;
    display: flex;
    justify-content: flex-start;
    gap: 15px;
    overflow-y: scroll;
}

.elements-carousel::-webkit-scrollbar {
    width: 0;
}

.elements-carousel .element-carousel {
    height: 230px;
}

.elements-carousel .element-carousel:last-child {
    margin-right: 25px;
}
