.story {
    width: 60%;
}

.story-short {
    width: 600px;
    height: min-content;
}

.story-short textarea {
    border-radius: 5px !important;
    height: 320px !important;
}

textarea#story {
    width: 100%;
    height: 450px;
    padding: 20px 25px;
    resize: none;
    border-radius: 0 0 5px 5px;
    border: 1px solid #1f1b16;
    background: transparent;
    outline: #1f1b16;
    font-family:
        Zen Kurenaido,
        sans-serif;
    font-size: 22px;
}

textarea:focus {
    outline: #1f1b16;
    font-family:
        Zen Kurenaido,
        sans-serif;
    font-size: 22px;
}

.attachment {
    padding: 10px 25px;
    background: #4d4b48e6;
    border-radius: 5px 5px 0 0;
}

input[type="file"] {
    display: none;
}

.input-file {
    width: 25px;
    height: 25px;
    margin-right: 18px;
    vertical-align: middle;
    display: inline-block;
}

label {
    font-size: 0; /* Делаем текст невидимым путем уменьшения его размера до 0 */
}

label[for="image"] {
    background: url("../../images/icons/story/img.png") 100% 100% / 100% 100%
        no-repeat;
}

label[for="image"]:hover {
    background: url("../../images/icons/story/img-hover.png") 100% 100% / 100%
        100% no-repeat;
}

label[for="video"] {
    background: url("../../images/icons/story/vid.png") 100% 100% / 100% 100%
        no-repeat;
}

label[for="video"]:hover {
    background: url("../../images/icons/story/vid-hover.png") 100% 100% / 100%
        100% no-repeat;
}

label[for="document"] {
    background: url("../../images/icons/story/doc.png") 100% 100% / 100% 100%
        no-repeat;
}

label[for="document"]:hover {
    background: url("../../images/icons/story/doc-hover.png") 100% 100% / 100%
        100% no-repeat;
}
